	import navHeader from './navHeader.vue';
	export default {
		name: "hello",
		components:{
			// uniTag
			navHeader
		},
		data() {
			return {
				name:"我的",
				imgUrl: "",
				date: '2019-06-18'
			}
		},
		onLoad() {

		},
		methods: {
			getUserInfo() {
				var that = this;
				uni.login({
					provider: 'weixin',
					success(res) {
						uni.getSetting({
							success(res) {
								if (res.authSetting['scope.userInfo']) {
									uni.getUserInfo({
										provider: 'weixin',
										withCredentials: true,
										success(res) {
											console.log(res);
											let userInfo = JSON.stringify(res.userInfo);
											wx.setStorageSync('userInfo', userInfo);
											that.imgUrl = res.userInfo.avatarUrl;
										}
									})
								} else {

								}
							}
						})
					}
				})
			},
			// 倒计时时间到触发事件
			getTimeup() {
				console.log("倒计时");
			},
			change(e) {
				console.log(e);
			},
			toClick(e) {
				console.log(e);
			},
			// 上传头像
			myUpload(rsp) {
                this.url = rsp.path; //更新头像方式一
                //rsp.avatar.imgSrc = rsp.path; //更新头像方式二
            }
		}
	}
